	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by J.Spin","subtitle":"ComputerVision / MachineLearning / Android(Java) / ReactJS / NodeJS","copyright":"© All rights reserved.","author":{"name":"제이스핀 (J.Spin)","instagram":"paeng___"},"disqusShortname":"","url":"https://lumen.netlify.com"}},"markdownRemark":{"id":"/Users/paeng/dev/web/nuggy875.github.io/src/pages/articles/2018-07-06---현재날짜 구하기/index.md absPath of file >>> MarkdownRemark","html":"<p>현재 시간을 필요로 하는 경우가 있다.<br>\n안드로이드에서는 이를 기본 라이브러리로 제공한다. (사실 자바 라이브러리)</p>\n<ol>\n<li>\n<p>현재 시간을 가져온다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-java\"><code class=\"language-java\">long now = System.currentMills();</code></pre>\n      </div>\n</li>\n<li>\n<p>Date형식으로 고친다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-java\"><code class=\"language-java\">Date mDate = new Date(now);</code></pre>\n      </div>\n</li>\n<li>\n<p>날짜, 시간을 가져오고 싶은 형식으로 가져올 수 있다.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-java\"><code class=\"language-java\">SimpleDateFormat simpleDate = new SimpleDateFormat(&quot;yyyy-MM-dd hh:mm:ss&quot;);\nString getTime = simpleDate.format(mDate);</code></pre>\n      </div>\n</li>\n</ol>\n<p>위와 같이 작성할 경우, getTime 변수에 <strong>2018-07-06 01:42:00</strong> 이런식으로 날짜를 가져올 수 있다.</p>\n<p>MM-dd 만 작성할 경우 “07-06” 이런식으로 Customize 할 수 있다.</p>","fields":{"tagSlugs":["/tags/android/","/tags/java/","/tags/simple-date-format/","/tags/안드로이드/"]},"frontmatter":{"title":"[안드로이드] 현재 날짜, 시간 구하기","tags":["Android","Java","SimpleDateFormat","안드로이드"],"date":"2018-07-06T01:52:32.169Z","description":"SimpleDateFormat 이용"}}},"pathContext":{"slug":"/posts/android/post-2"}}

